}

.table-cell {
  max-width: 199px;
  width: 100%;
  border: 1px solid #a6a6a6;
  padding: 4px;
  text-align: center;
  vertical-align: middle;
  overflow-wrap: break-word;
  font-size: 12px;
  text-transform: capitalize;
}

th {

